// Modal.tsx
import * as React from 'react';
import Dialog from '@mui/material/Dialog';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import "./_modal.scss";


const ModalClient: React.FC = () => {
    const [open, setOpen] = React.useState(true);


    const handleClose = () => {
        setOpen(false);
    };

    return (
            <Dialog
            open={open}>
            <DialogTitle id="alert-dialog-title">
                <div className={'dialog_title'}>
                    <h1>SMOKYJOY</h1>
                    <h1>18+</h1>
                </div>
            </DialogTitle>
            <DialogContent>
                <div className={'modal_text'}>
                    <h2 >      </h2>
                    <p className={'textP'}>      ,     ,       ,                   (.10     ). ,   ,    (. 20  15    ).
                    </p>
                </div>

            </DialogContent>
                <div className={'modal_buttons'}>
                    <button onClick={handleClose} type={"submit"} className={'button_modal button_modal_active'}>  18 </button>
                    <button  type={"submit"} className={'button_modal button_modal_close'}>
                          18 
                    </button>
                </div>


        </Dialog>

    );
};

export default ModalClient;